
.navbar{
    display: flex;
    align-items: center;
    justify-content:space-between;
    padding:0;
    height:auto;
    transition: all 0.5s ease-in-out;
}
.link{
    color: #c99822;
    font-size:18px;
    padding: 16px 12px;
}

.link:hover{
    /* background-color:#c99822 !important; */
    color:#000 !important;
}
.nav-center {
    background: #fff;
    box-shadow:0 5px 10px rgba(133, 133, 133, 0.1);
    padding:4px 14px;
}

.profileIcon{
  max-height:40px;
}

.dropdown .dropbtn {
    font-size: 18px;  
    border: none;
    outline: none;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
  }
.dropdown-content {
    display: none;
    position: absolute;
    min-width: 160px;
    background-color: #fff;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    top: 60px;
    right: -10px;
  }
  
  .dropdown-content a {
    float: none;
    color: #c99822;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
  }
  .dropdown-content a:hover{
      color:#000;
      background-color:#fff;
  }
  .dropdown:active{
      color: #fff;
  }
  .active {
    display: block;
  }