::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background: var(--clr-grey-10);
  color: #555;
  line-height: 1.5;
  font-size: 0.875rem;
  background-color: #f6f6f6;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}
h1 {
  font-size: 2rem;
  color: #c99822;
  /* display: flex; */
  margin: 20px 0 15px;
  box-shadow: 0 2px 2px #eee;
  padding: 12px ;
}
h2 {
  font-size: 1.8rem;
}
h3 {
  font-size: 1.25rem;
}
h4 {
  font-size: 0.875rem;
}
p {
  margin-bottom: 1.25rem;
  color: var(--clr-grey-5);
}
.main-color {
  color: #C99822;;
}
.sec-color {
  color: #666;
}
.main-bgcolor {
  background-color: #C99822;;
}
.bg-gold{
    background-color:#C99822;
    color: #fff;
}
.bg-gold:hover{
  background-color: #fff;
  color:#c99822;
}
.bg-dark{
  background-color:#000;
  color: #fff;
}
.bg-trans{
  background-color:transparent;
  color: #000;
}
.btn-danger:hover{
  background-color: #fff;
  color:#DC3545;
}

.btn-success:hover{
  background-color: #fff;
  color:#198754;
}
.btn-secondary:hover{
  background-color: #fff;
  color:#5C636A;
}

.error{
  color:red;
  font-size:1.5rem;
}

@media screen and (min-width: 800px) {
  h1 {
    font-size: 1.8rem;
  }
  h2 {
    font-size: 1.7rem;
  }
  h3 {
    font-size: 1.65rem;
  }
  h4 {
    font-size: 1rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
  @media (max-width:992px){
      .container{
        width: 100vw;
      }    
  }
}

