.night-div {
  background-color: #fff;
  box-shadow: rgb(150 150 150 / 45%) 0px 25px 20px -30px;
  border-radius: 5px;
  padding: 10px 0px;
}

.icons-div {
  height: 100%;
}

.icons-div svg {
  cursor: pointer;
}

.edit-icon {
  margin-left: 18px;
  color: #a37e26;
  margin-bottom: 5px;
  cursor: pointer;
}

.delete-icon {
  color: #c54545;
  margin-top: 3px;
  cursor: pointer;
}

.price-div {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: start;
  padding: 0 5px;
  font-size: 15px;
  margin-top: 18px;
  margin-bottom: 0px !important;
}

.price-div div {
  color: #666;
  font-weight: 500;
}
.price {
  text-decoration: line-through;
  margin-right: 5px;
}

.details {
  height: 50px;
  width: 99%;
  background-color: #fff;
  border-radius: 5px;
  margin: 3px 10px;
  padding: 10px 0 0 32px;
}

.extras-list {
  display: flex;
  justify-content: space-around;
  margin-top: 30px;
}

.details-div {
  max-height: 230px;
  overflow-y: auto;
}

.hide-details-div {
  display: none;
}

.delete-popup-overlay {
  z-index: 99999 !important;
}

.delete-popup-content {
  width: 35% !important;
  height: 35% !important;
}

.pop-background {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: -16px;
  left: 0;
  z-index: 100;
}

.delete-popup {
  width: 30%;
  height: 30%;
  position: absolute;
  left: 50%;
  top: 35%;
  transform: translate(-50%, 0);
  background-color: #fff;
  opacity: 1;
  padding: 20px;
  border-radius: 3px;
  z-index: 1000;
}

.delete-popup svg {
  width: 100%;
  font-size: 37px;
  color: #d49e1f;
}

.delete-popup button {
  width: 100px;
}

.delete-popup p {
  text-align: center;
  margin: 8px 0;
}
